.contact__container{
    background:  -webkit-linear-gradient(90deg, #334257 10%, #476072 70% );
    width: 100%;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* margin-top: 150px; */
}

@media screen 
and (min-width:421px){
    .contact_items{
        background:  -webkit-linear-gradient(90deg, #476072 10%, #334257 70% );
        width: 35%;
        height: 80px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        border-radius: 100px;
    }
    .email_contact_section, .phone_contact_section{
        width: 100%;
        color:#EEEEEE;
        font-weight: bolder;
        font-size: 2vw;
        cursor: pointer;
        text-decoration: none;
        -webkit-animation: tracking-in-contract-bck 4s cubic-bezier(0.215, 0.610, 0.355, 1.000) both infinite;
                animation: tracking-in-contract-bck 4s cubic-bezier(0.215, 0.610, 0.355, 1.000) both infinite;

    }
    .phone_text{
        text-decoration: none;
        color:#EEEEEE;
    }
    .email_contact_section:hover, .phone_contact_section:hover{
        background-color: #334257;
        /* width: 100%;
        height: 100px; */
        color: #EEEEEE;
        display: flex;
        /* font-size: 2.5vw; */
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        border-radius: 100px;
        box-shadow: 10px 20px 30px #548CA8;
    }
    @-webkit-keyframes tracking-in-contract-bck {
        0% {
          letter-spacing: 1em;
          -webkit-transform: translateZ(400px);
                  transform: translateZ(400px);
          opacity: 0;
        }
        40% {
          opacity: 0.6;
        }
        100% {
          -webkit-transform: translateZ(0);
                  transform: translateZ(0);
          opacity: 1;
        }
      }
    @keyframes tracking-in-contract-bck {
        0% {
          letter-spacing: 1em;
          -webkit-transform: translateZ(400px);
                  transform: translateZ(400px);
          opacity: 0;
        }
        40% {
          opacity: 0.6;
        }
        100% {
          -webkit-transform: translateZ(0);
                  transform: translateZ(0);
          opacity: 1;
        }
      }
      
      
      

    
}

