@media screen
    and (min-width: 421px){
        .questionsPage__container{
            width: 100%;
            min-height: 750px;
            height: min-content;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
        }

        .secction_story{  
        margin-top: 50px;
        width: 80%;  
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        }

        .question_secction_button{
            
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;
            
        }
        .question_I{
            background-color: #334257;
            border-radius: 20px;
            margin:5px;
            width:20%;
            color: #EEEEEE;
            font-weight: bolder;
            font-size: 1.3vw;
            cursor: pointer;
        }

        .question_I:hover{
            width: 20%;
            height: 70px;
            background-color: #334257;
            color: #EEEEEE;
            border-radius: 20px;
            font-size: 1.5vw;
            font-weight: bolder;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: 10px 20px 30px #548CA8;
        }
        
        .myHistory__container{
   
            width:100%;
            display: flex;
            min-height: 700px;
            height: min-content;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
        }
        .question_title_history{
            grid-area: titleText;
        }
        .text_history_section{
            grid-area: textSection;
        }
        .picture_history_section{
            grid-area: pictureSection;
        }
        .history_section{
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows:auto;
            grid-template-areas: 
            " titleText titleText"
            "textSection pictureSection";
            justify-content: flex-start;
            align-items: center;
        }
        
        .question_picture{
            border-radius: 0.5rem 0.5rem 0 0;
            width: 50%;
            object-fit: cover;
            max-height: max(20rem, 50vh);
            aspect-ratio: 4/3;
            mix-blend-mode: var(--card-blend-mode);
            width: 80%;
            opacity: 0.6;
    
        } 
        
        .text_history_section{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
        
        }
        
        .question_title_history{
            text-align:start;
            font-weight: bolder;
            font-size: 2vw;
            color:white;
            text-shadow: 1px 1px 2px black;
        }
        .question_text_history{
            text-align:justify;
            font-size: 1.3vw;
            color:white;
            font-style: oblique;
            font-weight: bolder;
        }
    }




/* --------------phone version-------------------- */

@media screen
    and (max-width: 420px) {
        .secction_story{
            margin-top: 40px;
            display: flex;
            flex-direction: column;
        }
        .myHistory__container{
            width: 100%;
            min-height: 700px;
            height: min-content;
            display: flex;
            flex-direction: column;
