            margin-top: 30px;            
        } 
        .text_history_section{
            display: flex;
            flex-direction: column;
            justify-content:space-between;
        }
        
        .question_title_history{
            text-align:start;
            font-weight: bolder;
            font-size: 4vw;
            color:white;
            text-shadow: 1px 1px 2px black;
        }
        .question_text_history{
            text-align:justify;
            font-size: 3vw;
            color:white;
            font-style: oblique;
            font-weight: bolder;
        }
}
